.about .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
}

.about .row .image {
  flex: 1 1 45rem;
}

.about .row .image img {
  width: 100%;
}

.about .row .content {
  flex: 1 1 45rem;
}

.about .row .content h3 {
  color: #444;
  color: var(--black);
  text-shadow: 0.4rem 0.4rem 0 rgba(0, 0, 0, 0.2);
  text-shadow: var(--text-shadow);
  font-size: 4rem;
  line-height: 1.8;
}

.about .row .content p {
  color: #777;
  color: var(--light-color);
  padding: 1rem 0;
  font-size: 1.5rem;
  line-height: 1.8;
}

.doctors .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.doctors .box-container .box {
  text-align: center;
  background: #fff;
  border-radius: 0.5rem;
  border: 0.2rem solid #16a085;
  border: var(--border);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  box-shadow: var(--box-shadow);
  padding: 2rem;
}

.doctors .box-container .box img {
  height: 20rem;
  border: 0.2rem solid #16a085;
  border: var(--border);
  border-radius: 0.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.doctors .box-container .box h3 {
  color: #444;
  color: var(--black);
  font-size: 2.5rem;
}

.doctors .box-container .box span {
  color: #16a085;
  color: var(--green);
  font-size: 1.5rem;
}

.doctors .box-container .box .share {
  padding-top: 2rem;
}

.doctors .box-container .box .share a {
  height: 5rem;
  width: 5rem;
  line-height: 4.5rem;
  font-size: 2rem;
  color: #16a085;
  color: var(--green);
  border-radius: 0.5rem;
  border: 0.2rem solid #16a085;
  border: var(--border);
  margin: 0.3rem;
}

.doctors .box-container .box .share a:hover {
  background: #16a085;
  background: var(--green);
  color: #fff;
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  box-shadow: var(--box-shadow);
}

.book .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
}

.book .row .image {
  flex: 1 1 45rem;
}

.book .row .image img {
  width: 100%;
}

.book .row form {
  flex: 1 1 45rem;
  background: #fff;
  border: 0.2rem solid #16a085;
  border: var(--border);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  box-shadow: var(--box-shadow);
  text-align: center;
  padding: 2rem;
  border-radius: 0.5rem;
}

.book .row form h3 {
  color: #444;
  color: var(--black);
  padding-bottom: 1rem;
  font-size: 3rem;
}

.book .row form .box {
  width: 100%;
  margin: 0.7rem 0;
  border-radius: 0.5rem;
  border: 0.2rem solid #16a085;
  border: var(--border);
  font-size: 1.6rem;
  color: #444;
  color: var(--black);
  text-transform: none;
  padding: 1rem;
}

.book .row form .btn {
  padding: 1rem 4rem;
}

.review .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(27rem, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.review .box-container .box {
  border: 0.2rem solid #16a085;
  border: var(--border);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
  padding: 2.5rem;
  background: #fff;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.review .box-container .box img {
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
  object-fit: cover;
  border: 0.5rem solid #fff;
}

.review .box-container .box h3 {
  color: #fff;
  font-size: 2.2rem;
  padding: 0.5rem 0;
}

.review .box-container .box .stars i {
  color: #fff;
  font-size: 1.5rem;
}

.review .box-container .box .text {
  color: #777;
  color: var(--light-color);
  line-height: 1.8;
  font-size: 1.6rem;
  padding-top: 4rem;
}

.review .box-container .box::before {
  content: "";
  position: absolute;
  top: -4rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #16a085;
  background: var(--green);
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  height: 25rem;
  width: 120%;
  z-index: -1;
}

.blogs .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.blogs .box-container .box {
  border: 0.2rem solid #16a085;
  border: var(--border);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
  padding: 2rem;
}

.blogs .box-container .box .image {
  height: 20rem;
  overflow: hidden;
  border-radius: 0.5rem;
}

.blogs .box-container .box .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.blogs .box-container .box:hover .image img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.blogs .box-container .box .content {
  padding-top: 1rem;
}

.blogs .box-container .box .content .icon {
  padding: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.blogs .box-container .box .content .icon a {
  font-size: 1.4rem;
  color: #777;
  color: var(--light-color);
}

.blogs .box-container .box .content .icon a:hover {
  color: #16a085;
  color: var(--green);
}

.blogs .box-container .box .content .icon a i {
  padding-right: 0.5rem;
  color: #16a085;
  color: var(--green);
}

.blogs .box-container .box .content h3 {
  color: #444;
  color: var(--black);
  font-size: 3rem;
}

.blogs .box-container .box .content p {
  color: #777;
  color: var(--light-color);
  font-size: 1.5rem;
  line-height: 1.8;
  padding: 1rem 0;
}

.footer .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.footer .box-container .box h3 {
  font-size: 2.5rem;
  color: #444;
  color: var(--black);
  padding: 1rem 0;
}

.footer .box-container .box a {
  display: block;
  font-size: 1.5rem;
  color: #777;
  color: var(--light-color);
  padding: 1rem 0;
  text-decoration: none;
}

.footer .box-container .box a i {
  padding-right: 0.5rem;
  color: #16a085;
  color: var(--green);
}

.footer .box-container .box a:hover i {
  padding-right: 2rem;
}

.footer .credit {
  padding: 1rem;
  padding-top: 2rem;
  margin-top: 2rem;
  text-align: center;
  font-size: 2rem;
  color: #777;
  color: var(--light-color);
  border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
}

.footer .credit span {
  color: #16a085;
  color: var(--green);
}

/* media queries  */
@media (max-width: 991px) {
  html {
    font-size: 55%;
  }

  .header {
    padding: 2rem;
  }

  section {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  #menu-btn {
    display: inline;
    display: initial;
  }

  .header .navbar {
    position: absolute;
    top: 115%;
    right: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
    box-shadow: var(--box-shadow);
    width: 30rem;
    border: 0.2rem solid #16a085;
    border: var(--border);
    background: #fff;
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    -webkit-transform-origin: top right;
            transform-origin: top right;
    transition: none;
  }

  .header .navbar.active {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    transition: 0.2s ease-out;
  }

  .header .navbar a {
    font-size: 2rem;
    display: block;
    margin: 2.5rem;
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
}

.navigation {
  background-color: rgb(238, 238, 105);
}
.nav-image {
  width: 50px;
}
.nav-link {
  color: rgb(233, 241, 234) !important;
}




.service-card {
  border: 2px solid paleturquoise;
  box-shadow: rgb(59, 55, 55);
  margin: 10px;
  padding: 0 10px;
}



.stars i {
  color: goldenrod !important;
}

.icon a {
  text-decoration: none;
}

.notFound {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login {
  width: 90%;
  max-width: 600px;
  margin: 0 auto;
  background-color: rgb(241, 179, 179) !important;
}

.details{
    display: flex;
    justify-content: center;
    align-items: center;
}
